header {
  width: auto;
  background-color: #2e8b57;
  border-radius: 13px;
  justify-content: space-between;
  align-items: center;
  padding: .1em;
  transition: all .4s ease-in-out;
  display: flex;
  box-shadow: 1px 2px 8px 3px #2f4f4f;
}

header:hover {
  box-shadow: 1px 2px 8px 3px #9acd32;
}

#mynav img {
  height: 50px;
  width: 50px;
  background: azure;
  border-radius: 20%;
  margin: 15px;
  padding-top: 5px;
  box-shadow: 3px 2px 4px 3px #9acd32;
}

.logo {
  text-align: left;
}

.logo:hover {
  animation: rotate .5s infinite;
}

#mynav li {
  color: #228b22;
  background: azure;
  border-style: groove;
  border-radius: 5px;
  margin: 5px;
  padding: 5px 5px 5px .5em;
  font-size: 22px;
  font-weight: 700;
  transition: box-shadow .5s;
  display: inline-flex;
}

#mynav li:hover {
  border-style: outset;
  box-shadow: 1px 2px 8px 3px #adff2f;
}

#mynav .active:hover {
  border-style: inset;
  top: 20px;
  box-shadow: 1px 2px 8px 3px #006400;
}

#mynav ul {
  text-align: center;
  flex-basis: 100%;
  align-self: flex-start;
  margin-top: 1rem;
  margin-bottom: 14px;
  padding: 1px;
}

#mynav .active {
  color: #9acd32;
  text-shadow: 3px 1px 12px azure;
  background: #f8f8ff;
  border-style: inset;
}

#mynav .active a {
  color: #9acd32;
}

#mynav li a:hover {
  text-decoration: underline;
}

#mynav .active a:hover {
  text-decoration: unset;
  animation: rotate .5s infinite;
}

#mynav li a {
  font-display: block;
  color: #228b22;
  float: right;
  white-space: nowrap;
  padding: .1em .2em;
